import React, { useEffect, useState } from 'react';
import { Button, Col, Container, Row, Table } from 'reactstrap';
import { Food } from '../Food/Food';

export const Meal = ({ title, editable, foods, units }) => {
  const [_foods, setFoods] = useState(foods);
  const [totalCarb, setTotalCarb] = useState(0);
  const [totalCals, setTotalCals] = useState(0);
  const [totalProt, setTotalProt] = useState(0);
  const [totalFat, setTotalFat] = useState(0);
  const [_units, setUnits] = useState(units);
  const [isEditable, setIsEditable] = useState(editable);

  useEffect(() => {
    setIsEditable(editable);
    agg();
  }, [editable, _foods]);

  const agg = () => {
    setTotalCals(
      _foods
        .map(food => 4 * (food.prot + food.carb) + 9 * food.fat)
        .reduce((cur, acc) => cur + acc)
        .toFixed(2),
    );
    setTotalProt(
      _foods
        .map(food => food.prot)
        .reduce((cur, acc) => cur + acc)
        .toFixed(2),
    );
    setTotalCarb(
      _foods
        .map(food => food.carb)
        .reduce((cur, acc) => cur + acc)
        .toFixed(2),
    );
    setTotalFat(
      _foods
        .map(food => food.fat)
        .reduce((cur, acc) => cur + acc)
        .toFixed(2),
    );
  };

  const handleOnSave = () => {
    setIsEditable(false);
    agg();
  };

  const handleOnAdd = () => {
    setFoods([
      ..._foods,
      { id: _foods.length + 1, units: _units, portion: 0, prot: 0, carb: 0, fat: 0 },
    ]);
  };

  const handleOnDelete = () => {
    setFoods(_foods.filter(food => food.id !== _foods.length));
  };

  const handleItemOnChange = item => {
    setFoods(
      _foods.map(food => {
        if (food.id === item.id) {
          return item;
        }
        return food;
      }),
    );
  };

  return (
    <Container>
      <Row>
        <h2>{title}</h2>
      </Row>
      <Row>
        <Table hover>
          <thead>
            <tr>
              <th>#</th>
              <th>name</th>
              <th>group</th>
              <th>portion</th>
              <th>calories</th>
              <th>proteins</th>
              <th>carbohydrates</th>
              <th>fat</th>
            </tr>
          </thead>
          <tbody>
            {_foods.map(item => (
              <Food
                key={item.id}
                item={item}
                editable={isEditable}
                handleItemOnChange={handleItemOnChange}
              ></Food>
            ))}
          </tbody>
        </Table>
      </Row>
      <Row>
        <Col>Total Calories: {totalCals}</Col>
        <Col>Total Proteins: {totalProt}</Col>
        <Col>Total Carbs: {totalCarb}</Col>
        <Col>Total Fats: {totalFat}</Col>
      </Row>
      <Row>
        <Col xs={8}>
          <Button color="success" onClick={handleOnSave}>
            Save
          </Button>
        </Col>
        <Col xs={4}>
          <Button color="danger" onClick={handleOnDelete}>
            -
          </Button>
          <Button color="primary" onClick={handleOnAdd}>
            +
          </Button>
        </Col>
      </Row>
    </Container>
  );
};
